.homes {
    grid-column: center-start/center-end;
    margin: 15rem 0;

    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(25rem, 1fr));
    grid-gap: 7rem;

    @media only screen and (max-width: $bp-small) {
        margin: 7rem 0;
    }
}
.home {
    background-color: $color-grey-light-1;

    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-row-gap: 3.5rem;
    
    &__img {
        width: 100%;

        grid-column: 1/-1;
        grid-row: 1/2;
        z-index: 10;
    }
    &__like {
        fill: $color-primary;
        height: 3rem;
        width: 3rem;
        margin: 1rem;
        z-index: 20;
        
        grid-column: 2/-1;
        grid-row: 1/2;
        justify-self: end;
    }
    &__name {
        font-family: $font-display;
        font-size: 1.6rem;
        text-align: center;
        padding: 1.25rem;
        background-color: $color-secondary;
        color: white;
        width: 80%;
        z-index: 20;
        transform: translateY(50%);

        grid-column: 1/-1;
        grid-row: 1/2;
        justify-self: center;
        align-self: end;
    }
    &__location, &__rooms {
        margin-top: 2rem;
    }
    &__location, &__rooms, &__area, &__price {
        font-size: 1.5rem;
        margin-left: 2rem;
        
        display: flex;
        align-items: center;

        svg {
            fill: $color-primary;
            height: 2rem;
            width: 2rem;
            margin-right: 1rem;
        }
    }
    &__btn {
        grid-column: 1/-1;
    }
}