.sidebar {
    background-color: $color-primary;
    grid-column: sidebar-start/full-start;
    grid-row: 1/-1;

    display: grid;
    grid-template-columns: 1fr;
    align-items: start;

    &__nav {
        display: grid;
        grid-template-columns: 1fr;
        justify-items: center;
        grid-gap: 2rem;

        @media only screen and (max-width: $bp-large) {
            grid-template-columns: repeat(4, 1fr);
            align-items: start;
        }
    }
    @media only screen and (max-width: $bp-large) {
        grid-column: 1/-1;
        grid-row: 1/2;
        justify-items: start;
        align-items: center;

        padding-left: .8rem;
    }
}
.nav-btn {
    border: none;
    background-color: $color-primary;
    // margin-top: .8rem;
    cursor: pointer;

    &__icon {
        fill: white;
        width: 4.5rem;
        height: 4.5rem;
        transition: all .4s;

        &:hover {
            transform: scale(1.3);
        }
    }
}
